.container {
  display: flex;
  justify-content: center;
  height: 95%;

  .timer-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 5em;

    #notation-timer {
      display: flex;
      align-items: center;
      flex-direction: column;
      #timer {
        text-align: right;
        height: 65%;
        width: 40%;
        border: none;
      }

      .notation{
        font-size: 50px;
        position: absolute;

        #notation-h{
          margin-left: 3.42em;
          margin-top: -1.47em;
        }
        #notation-m{
          margin-left: 7.7em;
          margin-top: -1.47em;
        }

        #notation-s{
          margin-left: 12.25em;
          margin-top: -1.47em;
        }
      }
    }

    #timer-button-container {
      display: flex;
      flex-direction: column;
      gap: 1em;
      #display-timer-container {
        display: flex;
        justify-content: center;
      }

      .absolute-timer {
        
        .notation-display {
          display: flex;
          justify-content: center;
          width: fit-content;
          font-size: 144px;
          // margin-left: .33em;
          // margin-top: -.2em;
          .hours {
            display: grid;
            grid-template-columns: 0.5fr 0.5fr 0.5fr;

            .notationH {
              font-size: 50px;
              padding-top: 1.86em;
              padding-right: 0.4em;
            }
          }

          .minutes {
            display: grid;
            grid-template-columns: 0.5fr 0.5fr 0.5fr;

            .notationM {
              font-size: 50px;
              padding-top: 1.86em;
              padding-right: 0.4em;
            }
          }

          #solo-seconds {
            display: flex;
          }
          
          .seconds {
            display: grid;
            grid-template-columns: 0.5fr 0.5fr 0.5fr;

            .notationS {
              font-size: 50px;
              padding-top: 1.86em;
              padding-right: 0.4em;
            }
          }
        }
      }

      #button-container {
        display: flex;
        justify-content: center;

        .fullscreen-mute {
          width: fit-content;
        }
      }
    }

    #start-button,
    #stop-button,
    #ok-button,
    #reset-button {
      font-size: 24px;
      width: 6em;
      height: 2.3em;
      margin: .5em;
      border-radius: 1em;
    }

    #start-button {
      background-color: #c1d3fe;
      border: none;
    }

    #reset-button {
      background-color: white;
      border-style: solid;
      border-color: #abc4ff;
      border-width: 3px;
    }

    #stop-button,
    #ok-button {
      background-color: #abc4ff;
      border: none;
    }

    #stop-button:hover,
    #ok-button:hover,
    #start-button:hover {
      background-color: #84a8ff;
    }

    #reset-button:hover {
      border-color: #84a8ff;
    }

    /* remove number input arrows Chrome, Safari, Edge, Opera */
    #timer::-webkit-outer-spin-button,
    #timer::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    /* Firefox */
    #timer[type="number"] {
      -moz-appearance: textfield;
    }

    #timer:focus {
      outline: none;
    }
  }
}

.logInput{
  width: 100%;
  height: 5em; 
  resize: none;
}

.charlimit{
  text-align: end;
}

.log-message{
  margin-bottom: 1em;
  font-weight: bold;
}

.submit-log{
  width: 103%;
  margin-top: 1em;
  border-radius: .5em;
  border: 2px solid #c1d3fe;
  background-color: #c1d3fe;
}

.submit-log:hover{
  border-color: #84a8ff;
  background-color: #84a8ff;
}
//Mobile Queries
//CSS specific for Safari on iOS
@supports (-webkit-touch-callout: none) {
  .logInput{
    width: 93%;
  }
}

@media only screen and (max-width: 713px) {
  .container {
    height: 90%;
    width: 100%;

    .timer-container {
      margin: 0 !important;

      #timer-button-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        margin-top: 10vh;

        #notation-timer{
          
          .inputs-boxes{

            .buttons{
              .up{

              }
              .down{
                
              }
            }

            .input-wrapper{
              .boxes{

              }

              .notation{
                position: absolute;
                #notation-h{
                  margin-top: -6.6em;
                  margin-left: 3.39em !important;
                }

                #notation-m{
                  margin-top: -4em;
                  margin-left: 3.39em !important;
                }

                #notation-s{
                  margin-top: -1.4em;
                  margin-left: 3.39em !important;
                }
              }
            }

          }
        }

        #display-timer-container {
          align-content: center;
          flex-wrap: wrap;

          .absolute-timer {
            height: 100%;
            margin-bottom: 3em;

            .notation-display {
              display: flex !important;
              flex-direction: column !important;
              margin-left: .55em;
              height: 3em;

              .hours {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                height: 0.9em;

                .notationH {
                  font-size: 50px;
                  height: 1.1em;
                }
              }

              .minutes {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                height: 0.9em;

                .notationM {
                  font-size: 50px;
                  height: 1.1em;
                }
              }

              #solo-seconds {
                display: flex;
                margin-right: .2em;
              }

              .seconds {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;

                .notationS {
                  font-size: 50px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-height: 667px) {
  .absolute-timer{
    margin-top: -3em !important;
    margin-bottom: 0em !important;
  }
}